import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import axios from 'axios';
import request from 'shared/request';
import { Kanji } from 'shared/types/Kanji';
import { IKanji } from './types';

const initialState: IKanji = {
  kanji: null,
  isLoading: false,
  error: null,
};

export const fetchKanji = createAsyncThunk(
  'kanji/fetchKanji',
  async (char: string | null, { rejectWithValue }) => {
    try {
      const response = await request.get<Kanji>(`/kanji/${char}`);
      return response.data;
    } catch (error) {
      if (axios.isAxiosError(error)) {
        return rejectWithValue(error.message);
      }
      return rejectWithValue(error);
    }
  },
);

const kanjiSlice = createSlice({
  name: 'kanjiList',
  initialState,
  reducers: { },
  extraReducers: (builder) => {
    builder
      .addCase(fetchKanji.pending, (state): IKanji => (
        {
          ...state,
          isLoading: true,
          error: null,
        }
      ))
      .addCase(fetchKanji.fulfilled, (state, action): IKanji => (
        {
          kanji: action.payload,
          isLoading: true,
          error: null,
        }
      ))
      .addCase(fetchKanji.rejected, () => (
        {
          kanji: null,
          isLoading: false,
          error: 'Error',
        }
      ));
  },
});

export default kanjiSlice.reducer;
