import React from 'react';
import S from './styles';

interface IKanjiItemProps {
  character: string;
  onClick: (char: string) => void;
}

const KanjiItem = ({ character, onClick } :IKanjiItemProps) => {
  const handleOnClick = () => onClick(character);

  return (
    <S.Element
      layoutId={character}
      key={character}
      whileHover={{ scale: 1.1 }}
      whileTap={{ scale: 0.95 }}
      onClick={handleOnClick}
    >
      {character}
    </S.Element>
  );
};

export default KanjiItem;
