import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import axios from 'axios';
import request from 'shared/request';
import { IKanjiList } from './types';

const initialState: IKanjiList = {
  kanjiList: [],
  level: 1,
  isLoading: false,
  error: null,
};

export const fetchList = createAsyncThunk(
  'kanjiList/fetchList',
  async (grade: number, { rejectWithValue }) => {
    try {
      const response = await request.get<string[]>(`/kanji/grade-${grade}`);
      return response.data;
    } catch (error) {
      if (axios.isAxiosError(error)) {
        return rejectWithValue(error.message);
      }
      return rejectWithValue(error);
    }
  },
);

const kanjiListSlice = createSlice({
  name: 'kanjiList',
  initialState,
  reducers: {
    setLevel: (state, action) => ({
      ...state,
      level: action.payload,
    }),
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchList.pending, (state): IKanjiList => (
        {
          ...state,
          isLoading: true,
          error: null,
        }
      ))
      .addCase(fetchList.fulfilled, (state, action): IKanjiList => (
        {
          kanjiList: action.payload,
          level: state.level,
          isLoading: true,
          error: null,
        }
      ))
      .addCase(fetchList.rejected, (state) => (
        {
          kanjiList: [],
          level: state.level,
          isLoading: false,
          error: 'Error',
        }
      ));
  },
});

export const { setLevel } = kanjiListSlice.actions;

export default kanjiListSlice.reducer;
