import React, { useState } from 'react';
import { setLevel } from 'entities/kanji-list';
import { useAppDispatch } from 'shared/lib/redux';
import S from './styles';

const Header = () => {
  const dispatch = useAppDispatch();

  const tabs = [
    { name: 'Grade 1', level: 1 },
    { name: 'Grade 2', level: 2 },
    { name: 'Grade 3', level: 3 },
    { name: 'Grade 4', level: 4 },
    { name: 'Grade 5', level: 5 },
    { name: 'Grade 6', level: 6 },
  ];

  const [selectedTab, setSelectedTab] = useState(tabs[0].name);

  const handleOnClick = (tab: { name: string; level: number; }) => () => {
    setSelectedTab(tab.name);
    dispatch(setLevel(tab.level));
  };

  return (
    <S.Nav>
      {tabs.map((tab) => (
        <S.Tab
          key={tab.name}
          onClick={handleOnClick(tab)}
        >
          <S.TabContent>{tab.name}</S.TabContent>
          {tab.name === selectedTab ? <S.Underline layoutId="underline" /> : null}
        </S.Tab>
      ))}
    </S.Nav>
  );
};

export default Header;
