import React from 'react';
import { useAppSelector } from 'shared/lib/redux';
import S from './styles';

const KanjiSidebar = () => {
  const { kanji } = useAppSelector((state) => state.kanji);

  return (
    <S.Aside isOpen={!!kanji}>
      <S.Symbol>{kanji?.kanji}</S.Symbol>
      <S.Meanings>{kanji?.meanings.slice(0, 3).join(', ')}</S.Meanings>
      <S.Readings>{kanji?.kun_readings?.slice(0, 3).join(', ')}</S.Readings>
      <S.Readings>{kanji?.on_readings?.slice(0, 3).join(', ')}</S.Readings>
      <S.Readings>{kanji?.name_readings?.slice(0, 3).join(', ')}</S.Readings>
    </S.Aside>
  );
};

export default KanjiSidebar;
