import React from 'react';
import { createHashRouter } from 'react-router-dom';
import Main from 'pages/main';
import MainLayout from 'widgets/main-layout';

const router = createHashRouter([
  {
    path: '/',
    element: <MainLayout />,
    children: [{
      path: '/',
      element: <Main />,
    }],
  },
]);

export default router;
