import React, { useCallback, useEffect } from 'react';
import KanjiItem from 'features/kanji-item/ui';
import { fetchKanji } from 'entities/kanji';
import { fetchList } from 'entities/kanji-list';
import { useAppDispatch, useAppSelector } from 'shared/lib/redux';
import S from './styles';

const KanjiList = () => {
  const dispatch = useAppDispatch();
  const { kanjiList, level } = useAppSelector((state) => state.kanjiList);

  useEffect(() => {
    dispatch(fetchList(level));
  }, [level]);

  const handleFetchKanjiOnClick = useCallback((char: string) => {
    dispatch(fetchKanji(char));
  }, []);

  return (
    <S.List>
      {kanjiList.map((character) => (
        <KanjiItem
          key={character}
          character={character}
          onClick={handleFetchKanjiOnClick}
        />
      ))}
    </S.List>
  );
};

export default KanjiList;
